.container-popup {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 84vw;
  left: 16%;
  top: 0;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  align-items: center;
  z-index: 3;
}

@media (max-width:1200px) {
  .container-popup {
    width: 80vw;
    left: 20%;
  }
}

.small-popup-container {
  z-index: 5 !important;
}

.pop-up {
  width: auto !important;
  background-color: white;
  padding: 20px;
  animation: zoomIn 0.3s;
  position: relative !important;
}

.small-pop-up {
  min-width: 350px;
  max-width: 95vw;
  background-color: white;
  min-height: 120px;
  border-radius: 5px;
  max-height: 95vh;
  animation: zoomIn 0.3s;
  overflow: auto;
}

@keyframes zoomIn {
  0% {
    transform: scale(0.2);
  }

  100% {
    transform: scale(1);
  }
}

@media (max-width: 1100px) {
  .pop-up {
    width: auto !important;
  }
}

@media (max-width:950px) {
  .pop-up {
    width: 70vw !important;
  }
}

@media (max-width: 900px) {
  .container-popup {
    width: 100vw;
    left: 0;
  }

  .container-popup.projectForm{
    padding-top: 10%;
  }

  .pop-up {
    width: 90vw !important;
    background-color: white;
    margin-top: 50px;
    min-height: 150px;
    max-height: 65vh;
    padding: 20px;
    animation: zoomIn 0.3s;
    overflow: auto;
  }
}

@media (max-width: 800px) {
  .pop-up {
