@font-face {
  font-family: "Cairo";
  src: url(../../assets/fonts/Cairo-Regular.ttf);
}

.trow:hover {
  box-shadow: rgba(33, 35, 38, 0.151) 0px 10px 10px -10px;
}

.trow:last-child:hover {
  box-shadow: rgba(33, 35, 38, 0.151) 0px 10px 10px -10px !important;
}

.thead {
  font-family: Cairo !important;
}

.Option {
  transition: all 0.9s;
  border: none !important;
  outline: none !important;
  text-transform: capitalize !important;
  border-radius: 5px;
}
.Option:hover {
  background-color: #f1f1f4 !important;
  border-radius: 5px;
}
