.badge-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 120px;
  width: 134px;
  background-color: #f1f1f5;
  padding: 5px 10px;
  border-radius: 5px;
  margin-right: 5px;
  margin-top: 5px;
  height: 32px;
  text-transform: capitalize;
  cursor: pointer;
}
